<template>
  <div id="sign-in">
    <head-top title="签到">
      <r-dropdown v-if="signState !==1 && signModel !== 1" ref="DropDown" class="head-right" slot="right">
        <van-icon ref="Scan" name="scan">
        </van-icon>
        <div slot="menu">
          <div class="head_menu_item" @click="scanOpen">扫一扫</div>
          <div class="head_menu_item" @click="codeOpen">签到码</div>
        </div>
      </r-dropdown>
    </head-top>
    <div class="bg"></div>
    <div class="content">
      <div class="success-info">{{$store.getters.student["StudentName"] + '，' + state}}</div>
      <div class="title">{{course["CourseName"]}}</div>
      <!-- <van-tag plain type="success" size="medium">#{{course['CourseName']}}</van-tag> -->
      <div class="line-info">
        <span>老师：{{course["TeacherName"]}}</span>
        <span>班级：{{course["ClassName"]}}</span>
      </div>
      <div class="line-info">
        <span>教室：{{course["ClassroomName"]}}</span>
        <span>
          时间：
          {{ course["CreateTime"] | formatDate(course["CreateTime"]) }}
        </span>
      </div>
    </div>

    <div v-if="signList.length ===1" class="sign_info">
      <van-tag plain type="primary" size="large">
        签到时间：
        {{ signList[0].Key["SignTime"] | formatDate(signList[0].Key["SignTime"]) }}
      </van-tag>
    </div>
    <div v-else-if="signList.length >1" class="sign_info">
      <div class="sign_item">
        <van-tag plain type="success" size="large" v-for="(item, i) in signList" :key="i" style="margin-bottom:3vw;">
          第{{item['SignNumber']}}次签到:
          {{ item.Key["SignTime"] | formatDate(item.Key["SignTime"]) }}
        </van-tag>
      </div>
    </div>

    <!-- 输入签到码进行签到 -->
    <van-popup v-model="signShow">
      <r-digit-input ref="digit" title="请在下方输入签到码进行签到" :code-length="4" btn-name="签到" code-name="签到码"
                     @submit="submitSignCode">
      </r-digit-input>
    </van-popup>
  </div>
</template>

<script>
import RDropdown from "@/components/r-dropdown";
import RDigitInput from "@/components/r-digit-input";
import { FuncType, sendToAPP } from "@/utils/interFlow";

export default {
  name: "",
  components: { RDropdown, RDigitInput },
  props: {},
  data () {
    return {
      state: "尚未签到",
      nowDateTime: "",
      course: this.$store.getters.course,
      signModel: 1,
      signState: 0,
      signList: [],
      signShow: false,
      signCode: ["", "", "", ""],
      signLoading: false
    };
  },
  created () {
    window.acceptQRcode = code => {
      console.log("接收签到码：", code);
      const codeJson = this.isJsonString(code);
      this.qrCodeSign(
        codeJson && codeJson["CIID"]
          ? codeJson["CIID"] + "," + codeJson["SignCode"]
          : codeJson,
        "scan"
      );
      return true;
    };
    this.getSignWay();
  },
  mounted () {
  },
  methods: {
    isJsonString (str) {
      try {
        if (typeof JSON.parse(str) === "object") {
          return JSON.parse(str);
        }
      } catch (e) {
      }
      return str;
    },
    // 开启扫一扫
    scanOpen () {
      this.$refs["DropDown"].hide();
      sendToAPP(FuncType.openQRcode, "");
    },
    // 打开输入邀请码
    codeOpen () {
      this.$refs["DropDown"].hide();
      this.signCode = ["", "", "", ""];
      this.signShow = true;
      setTimeout(() => {
        this.$refs["digit"].init();
      }, 0);
    },
    submitSignCode (arr) {
      this.qrCodeSign(arr.join(""), "code");
    },

    getSignWay () {
      this.signList = [];
      this.$mainHttp.sign.dataList({
        ciID: this.$store.getters.courseID,
        studentID: this.$store.getters.studentID
      }).then(res => {
        if (res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.signList.push(item);
          });
        }
        if (res.SignModel) {
          this.signModel = res.SignModel;
          this.signState = res.Result ? 1 : 0;
          this.autoSign();
        } else {
          this.state = "签到失败";
          this.$toast("获取签到状态失败~");
        }
      }).catch(() => {
        this.state = "签到失败";
        this.$toast("获取签到状态异常~");
      });
    },
    autoSign () {
      if (this.signState === 1) {
        this.state = "已签到";
      } else {
        if (this.signModel === 1) {
          this.qrCodeSign("");
          // } else {
          //   sendToAPP(FuncType.openQRcode, "");
        }
      }
    },

    qrCodeSign (code, signWay) {
      this.state = "签到中...";
      const codeStr = typeof code === "string" ? code : JSON.stringify(code);
      let reqData = {
        ciID: this.$store.getters.courseID,
        studentID: this.$store.getters.studentID,
        qrCodeStr: "",
        signCode: ""
      };
      if (signWay === "scan") {
        reqData.qrCodeStr = codeStr;
      } else if (signWay === "code") {
        reqData.signCode = codeStr;
      }
      this.$mainHttp.sign.postSign(reqData).then(res => {
        if (res.Result) {
          this.signState = 1;
          this.state = "签到成功";
          this.signShow = false;
          this.$toast("签到成功");
          // this.getSignList();
          this.getSignWay();
        } else {
          this.state = res.Info ? res.Info : "签到失败";
          if (res.Info) this.$toast(res.Info);
        }
      }).catch(() => {
        this.state = "操作失败";
      });
    },
    getSignList () {
      // this.signList = [];
      // this.$gatewayHttp.sign.dataList({
      //   CIID: this.$store.getters.courseID,
      //   studentID: this.$store.getters.studentID
      // }).then(res => {
      //   if (res.Result) {
      //     if (res.Datas && res.Datas.length > 0) {
      //       res.Datas.forEach(item => {
      //         this.signList.push(item);
      //       });
      //     }
      //   } else {
      //     this.$toast("获取签到数据失败");
      //   }
      // }).catch(() => {
      //   this.$toast("获取签到数据异常");
      // });
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");

#sign-in {
  width: 100%;
  min-height: calc(98vh);
  padding-bottom: 2vh;
  margin-bottom: -2vh;
  background: #ebf0ef;

  .head-right {
    .head_menu_item {
      color: #323233;
      background-color: #fff;
      border: 1px solid #ebedf0;
      height: 40px;
      font-size: @font-info;
      line-height: 40px;
      width: 50px;
      padding: 0 15px;
      text-align: center;

      &:focus,
      &:hover {
        color: #00cbab;
        background: #ecf5ff;
      }
    }
  }

  .bg {
    width: 100%;
    height: 27vh;
    background: url("../../../assets/qiandaobg.png");
    background-size: cover;
    margin-bottom: 48px;
  }

  .content {
    width: 85vw;
    background: rgba(255, 255, 255, 1);
    margin: 0 auto;
    overflow: hidden;
    padding-bottom: 15px;

    .success-info {
      font-size: @font-title * 1.5;
      margin-top: 20px;
      line-height: 2;
      color: rgba(0, 203, 171, 1);
    }

    .title {
      font-size: @font-title;
      font-family: MicrosoftYaHei;
      line-height: 2.5;
    }

    .line-info {
      width: 100%;
      margin-bottom: 6px;

      span {
        font-size: @font-info;
        display: inline-block;
        width: 50%;
        text-align: left;

        &:first-child {
          width: 42%;
          margin-left: 6%;
        }
      }
    }

  }

  .sign_info {
    margin: 0 7.5vw;
    padding-top: 4vw;
    text-align: left;

    .sign_item {
      margin: 3vw auto;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }
}
</style>



