<template>
  <div class="r-dropdown" ref="downMain">
    <div ref="downInfo" class="down-info" @click="toggleMenu($event)">
      <slot></slot>
    </div>

    <div ref="downMenu" class="dropdown-menu" v-show="showMenu" :style="positionStyle">
      <slot name="menu"></slot>
    </div>
  </div>
</template>

<script>
/****
 * 功能：简单挂载弹窗
 * 作者：gfy
 * 参考1：https://blog.csdn.net/april_4/article/details/79985412
 * 参考2：https://blog.csdn.net/weixin_45529161/article/details/102699702
 * 创建日期：2019-12-25
 *  */

export default {
  name: "r-dropdown",
  data() {
    return {
      showMenu: false,
      positionStyle: { top: "20px", left: "20px" }
    };
  },
  props: {
    selected: {}
  },
  created() {},
  mounted() {
    this.selectedOption = this.selected;
    document.addEventListener("click", this.hidePanel, false);
  },
  destroyed() {
    document.removeEventListener("click", this.hidePanel, false);
  },
  methods: {
    updateOption(option) {
      this.selectedOption = option;
      // this.showMenu = false;
      this.$emit("updateOption", this.selectedOption);
    },
    toggleMenu(event) {
      this.showMenu = !this.showMenu;
      let screenWidth = document.body.clientWidth;
      let screenHeight = document.body.clientHeight;
      setTimeout(() => {
        let info = this.$refs["downInfo"].getBoundingClientRect();
        let menu = this.$refs["downMenu"].getBoundingClientRect();
        // console.log("info:", info);
        // console.log("menu:", menu);
        let x = 0;
        let y = 0;

        if (info.width >= menu.width) {
          // 长度绝对充足
          x = info.left + (info.width - menu.width) / 2.0;
        } else {
          // 长度可能存在不够的情况下
          if (
            screenWidth - (info.left + (info.width - menu.width) / 2.0) >=
            menu.width
          ) {
            x = info.left + (info.width - menu.width) / 2.0;
          } else {
            x = screenWidth - menu.width - 5;
          }
        }

        //高度是否绝对充足
        if (menu.height >= screenHeight - info.bottom + 5) {
          y = info.top - menu.height + 5;
        } else {
          // 高度可能存在不够的情况下
          y = info.height + info.top - 5;
        }

        this.positionStyle = {
          top: y + "px",
          left: x + "px"
        };
      }, 10);
    },
    closeBtnGroup() {
      this.showMenu = false;
    },
    hide() {
      setTimeout(() => {
        this.showMenu = false;
      }, 150);
    },
    hidePanel(e) {
      if (!this.$refs.downMain.contains(e.target)) {
        //点击除弹出层外的空白区域
        this.showMenu = false;
      }
    }
  }
};
</script>



