<template>
  <div class="r-digit">
    <p class="digit-title">{{title}}</p>
    <p class="digit-remark">{{subTitle}}</p>
    <div class="digit-main">
      <div>
        <van-field ref="topText" type="digit" class="digit-top" style="" :maxlength="codeLength"
                   v-model="codeValue" @blur="isFocus=false" @focus="isFocus=true" @input="textChange">
        </van-field>
      </div>
      <div class="digit-input">
        <van-field type="digit" disabled v-for="(item, index) in codeArr.length" :key="index"
                   v-model="codeArr[index]" maxlength="1"
                   :class="['digit-text', 'len_' + codeArr.length, isFocus && index === codeValue.length ? 'digit-focus':'' ]"
                   :style="{width: '10vw', margin: '0 ' + 20 / codeArr.length + 'vw'}">
        </van-field>
      </div>
    </div>
    <div class="digit-operate">
      <van-button type="primary" size="small" class="digit-btn" :loading="invitedLoading" @click="submitCode">
        {{btnName}}
      </van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "r-digit-input",
  props: {
    title: {
      type: String,
      default: ""
    },
    subTitle: {
      type: String,
      default: ""
    },
    codeLength: {
      type: Number,
      default: 1
    },
    btnName: {
      type: String,
      default: "操作"
    },
    codeName: {
      type: String,
      default: "邀请码"
    }
  },
  data () {
    return {
      invitedLoading: false,
      codeValue: "",
      isFocus: false
    };
  },
  computed: {
    codeArr () {
      let arr = [];
      arr = this.codeValue.split("");
      for (let i = arr.length; i < this.codeLength; i++) {
        arr.push("");
      }
      return arr;
    }
  },
  methods: {
    init () {
      this.codeValue = "";
      setTimeout(() => {
        this.$refs["topText"].focus();
      }, 0);
    },
    /**
     * 输入框替换当前值
     * @param index
     * @param e
     */
    onTextInput (index, e) {
      this.codeArr.splice(index, 1, e.data);
    },
    /**
     * 输入框自动切换
     * @param index
     */
    onInput (index) {
      // index < 5 ，如果是第6格，不触发光标移动至下一个输入框。
      if (this.codeArr[index] && index < this.codeLength - 1) {
        this.$refs["ref" + (index + 1)][0].focus();
      }
    },
    /**
     * 输入框退格自动切换
     * @param index
     */
    onDelete (index) {
      // 如果是第1格，不触发光标移动至上一个输入框
      if (index > 0) {
        this.$refs["ref" + (index - 1)][0].focus();
      }
    },
    textChange () {
      if (this.codeValue.length === this.codeLength) {
        this.submitCode()
      }
    },
    submitCode () {
      const index = this.codeArr.find(r => !r);
      if (index > -1) {
        this.$toast("请输入" + this.codeName);
      } else {
        this.$emit("submit", this.codeArr);
      }
    }
  }
};
</script>



